@use '../../../styles/vars/index' as v;

.root {
    display: flex;
    background-color: v.$clr-light-shades;
    padding: 1rem;
    border: 3px dashed v.$clr-black;
    box-shadow: 0px 0px 10px 3px v.$clr-dark-accent;
    
    &.hovered {
        background-color: v.$clr-main;
        cursor: pointer;
    }

    .image {
        width: 100%;
        height: 350px;
        overflow: hidden;
    }

    .image img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center
    }

    .content {

        .shortDesc {
            font-family: v.$font-roboto-bold;
            font-style: italic;
        }

        a {
            color: v.$clr-black;
            text-decoration: none;
        }
}
}

    .price {
        color: v.$clr-white;
        background-color: v.$clr-price;
        padding: 0.4rem 0.8rem;
        border: 2px dashed v.$clr-black;
        font-family: v.$font-roboto-bold;
        display: flex;
        gap: 1rem;
    }


.grid {
    flex-direction: column;
    gap: 1rem;

    h3,p {
        margin: 0;
        padding: 0;
    }

    .content {
        display: flex;
        align-items: center;
        flex-direction: column;
        gap: 0.5rem;

        .title {
            font-size: v.$fnt-size-medium;
            font-family: v.$font-roboto-bold;
        }
    }
}

.list {
    gap: 1rem;

    h3,p {
        margin: 0;
        padding: 0;
    }

    .image {
        width: 400px;
    }

    .content {
        display: flex;
        width: 100%;;
        justify-content: space-around;
        align-items: center;
        flex-direction: row;
        gap: 3rem;

        .title {
            font-size: v.$fnt-size-medium;
            font-family: v.$font-lobster;
        }
    }
}

@media (width < 1000px) {
    .list {

        .image {
            width: 200px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .content {
            flex-direction: column;
            gap: 1rem;

            .title {
                font-size: v.$fnt-size-regular-p;
                font-family: v.$font-lobster;
            }
            .shortDesc {
                font-size: v.$fnt-size-small;
            }

            .priceContainer {
                .price {
                    font-size: v.$fnt-size-small;
                    text-wrap: nowrap;
                }
            }
        }
    }
}