@use '../../../styles/vars/index' as v;


.root {
    position: relative;
    background-color: v.$clr-light-shades;
    padding: 1rem;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    border-bottom: 3px dashed v.$clr-black;

    h2,ul {
        margin: 0;
        padding: 0;
    }

    h2 {
        font-size: v.$fnt-size-bigger;
        font-family: v.$font-libertinus;
        text-transform: capitalize;
    }

}

.views {
    position: absolute;
    right: 1rem;
    display: flex;
    list-style: none;
    gap: 1rem;
}

@media (width < 600px) {
    .root {
        flex-direction: column;
        gap: 1rem;

        h2 {
            margin-left: unset;
            font-size: v.$fnt-size-medium;
        }
    }
    .views{
        position: unset;
        margin-left: unset;
    }
}