@use '../../../styles/vars/index' as v;

.root {
    background-color: v.$clr-light-shades;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-inline: 1rem;
    border-bottom: 5px dashed v.$clr-black;

    .logo h2{
        font-size: v.$fnt-size-big;
        font-family: v.$font-libertinus;
        text-transform: uppercase;

        a {
            text-decoration: none;
            color: v.$clr-black
        }

        a:hover {
            color: v.$clr-light-accent;
        }
    }

    .nav ul {
        display: flex;
        align-items: center;
        list-style: none;
        gap: 1rem;
        padding: 0;
        margin: 0;
        font-family: v.$font-lobster;

        .user {
            font-size: v.$fnt-size-medium;
            text-transform: capitalize;
            color: v.$clr-main;
        }

        .cart {
            position: relative;
            .quantity {
                position: absolute;
                top: -10px;
                right: -10px;
                background-color: v.$clr-light-accent;
                color: v.$clr-black;
                border-radius: 50%;
                width: 20px;
                height: 20px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: v.$fnt-size-small;
            }
        }

        a {
            font-size: v.$fnt-size-medium;
            text-decoration: underline dashed 3px transparent;
            color: v.$clr-black;
            transition: color 0.3s ease-in-out, text-decoration 0.3s ease-in-out;
        }

        a:hover {
            color: v.$clr-light-accent;
            text-decoration: underline dashed v.$clr-light-accent 3px;
        }

    }
}

@media (width <= 768px) {
    .root {
        flex-direction: column;
        gap: 1rem;
        padding-bottom: 1rem;
        text-align: center;

        .nav{
            display: flex;
            flex-direction: column;
            gap: 0.5rem;
            align-items: center;
            justify-content: center;
        }

        .nav .navList {
            display: none;
            flex-direction: column;

            &.active {
                display: flex;
            }
        }
    }
}