@use '../../../styles/vars/index' as v;

$imgWidth: 100px;

.actions {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.root {
    display: grid;
    gap: 1rem;
    padding: 1rem;
    border: 1px dashed v.$clr-black;
    align-items: center;

    .productImage {
        width: $imgWidth;
        img {
            width: 100%;
        }
    }

    .price {
        font-weight: bold;
        font-size: v.$fnt-size-regular-p;
    }

    .quantity, .comment {
        display: flex;
        flex-direction: column;
        align-items: center;
        cursor: pointer;

        .fakeInput {
            padding: 0.2rem 1rem;
            border: 1px solid v.$clr-black;
            font-weight: bold;
            padding-bottom: 0.2rem;
            height: v.$fnt-size-regular-p;
        }
        input {
            border: 2px dashed v.$clr-black;
            padding: 0.2rem;
            text-align: center;
        }

    }

    .actions {
        display: flex;
        gap: 1rem;
    }


    .quantity {

        input {
            width: 40px;
        }
    }

    .productInfo {
        h3,p {
            margin: 0;
            margin-bottom: 0.3rem;
        }

        p {
            font-size: v.$fnt-size-small;
            font-weight: bold;
            span {
                opacity: 0.5;
                display: inline-block;
                font-weight: lighter;
                padding-right: 0.5rem;
            }
        }
    }
            
}

.cart {
        grid-template-columns: $imgWidth repeat(5, 1fr);
}
.checkout {
    grid-template-columns: $imgWidth repeat(4, 1fr);
}


@media(width < 760px){
    .root{
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        padding: 1rem;
        border-block: 2px solid v.$clr-black;
        p {
            margin: 0;
        }
        img {
            display: none;
        }
    }
}