@use '../../../styles/vars/index' as v;

.root {
    color: v.$clr-black;
    background-color: v.$clr-light-shades;
    font-family: v.$font-lobster;
    font-size: v.$fnt-size-regular-p;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border-top: 5px dashed v.$clr-black;
    text-align: center;
    p {
        padding: 0;
        margin: 0;
    }
}

@media (width < 500px) {
    .root p span{
        display: block;    
    }
}