@use '../../../styles/vars/index' as v;

.root {
    display: flex;
    border: 2px dashed v.$clr-black;
    border-radius: 1rem;
    overflow: hidden;
    padding: 0.2rem;
    transition: border 0.3s ease-in-out;

    &:hover {
        border-color: v.$clr-light-accent;
    }

}

.root:focus-within {
    border: 2px solid v.$clr-light-accent;
    box-shadow: 0px 0px 10px 3px v.$clr-dark-accent;
}

.form {
    display: flex;
    justify-content: center;

    input {
        border: none;
        outline: none;
        border-radius: 0.5rem;
        font-family: v.$font-roboto;
    }

    &.hidden {
        display: none;
    }
}

.btn {
    $size: 2rem;
    font-size: v.$fnt-size-regular-p;
    width: $size;
    height: $size;
    margin: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in-out;

    &:hover {
        color: v.$clr-light-accent;
    }

    .closed {
    border: none;
    }

    .close {
        border-left: 2px dashed v.$clr-black;
    }

    .search {
        border-right: 2px dashed v.$clr-black;
    }
}
