@use '../../../styles/vars/index' as v;

.root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    gap: 1rem;
    position: relative;
    overflow: hidden;
    
    .hero {
        position: absolute;
        z-index: -1;
        img {
            object-fit: fill;
            object-position: center;
        }
    }

    .content {
        h1{
            color: v.$clr-white;
            font-size: v.$fnt-size-bigger;
            font-family: v.$font-roboto-bold;
            text-shadow: 1px 1px 5px v.$clr-black;
        }

        p {
            color: v.$clr-white;
            text-shadow: 1px 1px 5px v.$clr-black;
            font-family: v.$font-roboto-bold;
            font-style: italic;
            text-align: center;
        }

        strong {
            color: v.$clr-light-accent;
        }

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
}

@media (width < 600px) {
    .root .content{ 
    h1 {
        text-align: center;
    }
}
}