@use '../../../styles/vars/index' as v;

.form {
    background-color: v.$clr-light-shades;
    border-left: 3px dashed v.$clr-black;
    border-right: 3px dashed v.$clr-black;
    fieldset {
        border: none;
    }

    legend {
        font-size: v.$fnt-size-regular-p;
        font-family: v.$font-roboto-bold;
        text-transform: uppercase;
        border: 2px dashed v.$clr-black;
        padding: 0.25rem 0.5rem;
    }

    .wrapper {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .actionBtns{
            display: flex;
            justify-content: flex-end;
        }

        .order {
            padding: 1rem;
            border: 2px dashed v.$clr-black;

            .orderList {
                display: flex;
                flex-direction: column;
                gap: 1rem;
                list-style: none;
                margin: 0;
                padding: 1rem;
            }
        }

        .contact {
            border: 2px dashed v.$clr-black;
            input[type="text"] {
                border: 1px dashed v.$clr-black;
            }
            label {
                span{
                    font-weight: bold;
                    padding-bottom: 0.25rem;
                }
                padding: 0.5rem 1rem;
                display: flex;
                flex-direction: column;
            }
        }

        .summaryPrice {
            padding: 1rem;
            border: 2px dashed v.$clr-black;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .summaryPriceItem {
                margin: 0;
                padding: 0;
                list-style: square;
                li {
                    display: grid;
                    grid-template-columns: 1fr repeat(3, 100px);
                    padding: 0.5rem;
                    &.total {
                        border-top: solid 1px v.$clr-black;
                        font-weight: bold;
                    }
                }
            }
        }
    }
    }



@media(width < 1000px) {
    .formRoot {
        padding: 1rem;
        .wrapper {
            align-items: center;
        }
    }
}

@media(width < 760px ){
    .form {
        legend {
            text-align: center;
        }
        .wrapper {
            .contact{
                text-align: center;
                input[type="text"] {
                    height: 44px;
                }
            }
            .summaryPrice{
                font-size: v.$fnt-size-small;
               .summaryPriceItem {
                   li {
                        grid-template-columns: repeat(4, 1fr);
                        gap: 25px;
                   }
               } 
            }

            .actionBtns{
                justify-content: center;
            }
            .order {
                .orderList {
                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}