@use '../../../styles/vars/index' as v;


.root {
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .content {
        font-family: v.$font-roboto;
        padding: 2rem;
        display: flex;
        flex-direction: column;

        .title {
            text-align: center;
            font-size: v.$fnt-size-medium;
        }

        div:has(img) {
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 200px;
            }
        }

        h3,p {
            margin: 0;
            padding: 0.5rem 0;
        }

        span {
            text-transform: capitalize;
            font-family: v.$font-roboto-bold;
        }

    }

    .card {
        position: relative;
        background-color: v.$clr-light-shades;
        padding: 2rem;
        border: 2px dashed v.$clr-black;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    .btn {
        cursor: pointer;
        padding: 0.4rem 0.8rem;
        background: v.$clr-main;
        border: 2px solid transparent;
        &:hover {
            border: 2px dashed v.$clr-black;
        }
        &:active {
            background-color: v.$clr-light-shades;
        }
        
    }

    .actions {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .closeBtn {
        position: absolute;
        right: 1rem;
        top: 1rem;
        gap: 1rem;

        .close {
            background-color: v.$clr-onSale;
        }
        
    }

}