@use '../../../styles/vars/index' as v;

.root {
    display: flex;
    flex-direction: column;
}


.searchPhrase {
    font-size: v.$fnt-size-bigger;
    font-family: v.$font-roboto-bold;
    text-align: center;
    background-color: v.$clr-light-shades;
    padding: 0.5rem;
    margin: 0;
    border-bottom: 1px dashed v.$clr-black;
}

.noSearch {
    font-size: v.$fnt-size-bigger;
    font-family: v.$font-roboto-bold;
    text-align: center;
    background-color: v.$clr-light-shades;
    padding: 2rem;
    margin: 0;
}

.noProducts {
    font-size: v.$fnt-size-bigger;
    font-family: v.$font-roboto-bold;
    text-align: center;
    background-color: v.$clr-light-shades;
    padding: 2rem;
    margin: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}