@use '../../../styles/vars/index' as v;

.root {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    gap: 1rem;

    .loader {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 8px solid v.$clr-main;
        border-top-color: v.$clr-light-accent;
        animation: spin 1s linear infinite;
        @keyframes spin {
            to {
                transform: rotate(360deg);
            }
        }
    }
}