@use '../../../styles/vars/index' as v;

.root {
    display: grid;
    place-content: center;
    place-items: center;
    padding: 2rem;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        /* display: none; <- Crashes Chrome on hover */
        -webkit-appearance: none;
        margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
    }

    input[type=number] {
        -moz-appearance: textfield;
        appearance: none;
    }
}

.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px dashed v.$clr-black;
    background-color: v.$clr-light-shades;
    padding: 2rem;

    .info {
        display: flex;
        align-items: center;
        gap: 2rem;

        .imagesContainer {
            max-width: 400px;

            button:before,
            button:before {
            color: v.$clr-black;
            }

            .image {

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                    object-position: center
                }
            }
        }

        .content {
            width: 400px;
            display: flex;
            flex-direction: column;
            padding: 1rem;
            justify-content: center;

            .colors {
                padding-block: 2rem;
            }

            .colors ul{
                margin: 0;
                padding: 0;
                list-style: none;
                display: flex;
                justify-content: space-evenly;
                li label {
                    padding: 1rem 3rem;
                    border: 3px dashed v.$clr-black;
                    &.active {
                        border: 3px solid v.$clr-light-accent;
                    }
                    cursor: pointer;
                    input[type="radio"] {
                        display: none;
                    }
                }

                li:has(label[data-clr='red']) label {
                    background-color: v.$clr-product-red;
                }
                li:has(label[data-clr='green']) label {
                    background-color: v.$clr-product-green;
                }
                li:has(label[data-clr='blue']) label {
                    background-color: v.$clr-product-blue;
                }
                li:has(label[data-clr='black']) label {
                    background-color: v.$clr-product-black;
                }
                li:has(label[data-clr='white']) label {
                    background-color: v.$clr-product-white;
                }
                li:has(label[data-clr='yellow']) label {
                    background-color: v.$clr-product-yellow;
                }
                li:has(label[data-clr='grey']) label {
                    background-color: v.$clr-product-gray;
                }
                li:has(label[data-clr='silver']) label {
                    background-color: v.$clr-product-silver;
                }
                li:has(label[data-clr='gold']) label {
                    background-color: v.$clr-product-gold;
                }

                li:has(input:checked) label {
                    border: 3px solid v.$clr-light-accent;
                }
            }

            .description {
                span {
                    text-transform: capitalize;
                    padding-bottom: 0.5rem;
                    display: block;
                    font-family: v.$font-roboto-bold;
                }
            }

            .productHeader {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    gap: 1rem;

                    h2,p {
                        margin: 0;
                    }
            }

            .priceContainer {
                display: flex;
                gap: 1rem;

                .price {
                    color: v.$clr-white;
                    background-color: v.$clr-price;
                    padding: 0.4rem 0.8rem;
                    border: 2px dashed v.$clr-black;
                    font-family: v.$font-roboto-bold;
                }
            }
            .actions {
                display: flex;
                align-items: center;
                flex-direction: column-reverse;
                gap: 1rem;
                    .quantity {
                    display: flex;
                    align-items: center;
                    gap: 0.5rem;

                    button {
                        cursor: pointer;
                        padding: 0.4rem 0.8rem;
                        background: v.$clr-main;
                        border: 2px solid transparent;
                        &:hover {
                            border: 2px dashed v.$clr-black;
                        }
                        &:active {
                            background-color: v.$clr-light-shades;
                        }
                    }

                    input[type="number"] {
                        width: 40px;
                        text-align: center;
                        border: 2px dashed v.$clr-black;
                        padding: 0.4rem 0.8rem;
                    }
                }
            }
        }
    }
}

@media (width < 900px) {
    .root {
        width: 100%;
        background-color: v.$clr-light-shades;
    }
    .product {
        padding-block: 2rem;
        .info {
            flex-direction: column;
            align-items: center;

            .image {
                width: 100%;
            }
            .content {
                width: 100%;
                padding: 0;
                margin: 0;
                .colors ul li {
                    label {
                        padding: 0.5rem 2rem;
                    }
                }
            }

        }
    }
}