@use '../../../styles/vars/index' as v;

.root {
    padding: 0.7em 1.4rem;
    border: dashed 4px transparent;
    background-color: v.$clr-light-accent;
    color: v.$clr-white;
    font-family: v.$font-roboto;
    font-size: v.$fnt-size-regular;
    text-decoration: none;
    cursor: pointer;
    transition: border 0.3s ease-in-out;

    &:hover {
        border: dashed 4px v.$clr-black;
    }
    
    &:active {
        border: dashed 4px v.$clr-light-shades;
        box-shadow: inset 0px 0px 10px 3px v.$clr-dark-accent;
    }
}

.menuBtn {
    display: none;
    font-size: v.$fnt-size-medium;
    border: none;
    background-color: transparent;
    cursor: pointer;
    transition: color 0.3s ease-in-out;

    &.active {
        color: v.$clr-light-accent;
    }

    &:hover {
        color: v.$clr-light-accent;
    }
}

@media (width <= 768px) {
    .menuBtn {
        display: block;
    }
}

.viewBtn {
    padding: 0.5rem;
    font-size: v.$fnt-size-regular-p;
    border: 2px dashed v.$clr-black;
    background-color: v.$clr-light-shades;
    cursor: pointer;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:hover {
        color: v.$clr-light-accent;
        border-color: v.$clr-light-accent;
    }

    &.active {
        color: v.$clr-light-accent;
        border-color: v.$clr-light-accent;
    }
}

.viewMore {
    @extend .root;
    background-color: v.$clr-dark-accent;
    &:hover {
        background-color: v.$clr-main;
    }
}

@media (width <= 500px) {
    .viewMore {
        font-size: v.$fnt-size-small;
    }
}

.removeProduct {
    padding: 0.5rem;
}