@use '../../../styles/vars/index' as v;

.form {
    background-color: v.$clr-light-shades;
    border-left: 3px dashed v.$clr-black;
    border-right: 3px dashed v.$clr-black;
    height: 100%;

    fieldset {
        border: none;
    }

    legend {
        font-size: v.$fnt-size-regular-p;
        font-family: v.$font-roboto-bold;
        text-transform: uppercase;
        border: 2px dashed v.$clr-black;
        padding: 0.25rem 0.5rem;
    }

    .wrapper {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        .actionBtns{
            display: flex;
            justify-content: flex-end;
        }

        .order {
            padding: 1rem;
            border: 2px dashed v.$clr-black;
        }

        .orderList {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            list-style: none;
            margin: 0;
            padding: 1rem;
        }
    }
    }



@media(width < 1000px) {
    .formRoot {
        padding: 1rem;
        .wrapper {
            align-items: center;
        }
    }
}

@media(width < 760px ){
    .form {
        legend {
            text-align: center;
        }
        .wrapper {
            .actionBtns{
                justify-content: center;
            }
            .order {
                .orderList {
                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}