//Fonts

$font-libertinus: 'Libertinus_Keyboard', monospace;
$font-lobster: 'Lobster', cursive;
$font-roboto: 'Roboto', sans-serif;
$font-roboto-bold: 'Roboto-Bold', sans-serif;

//Libertinus_Keyboard

@font-face {
  font-family: 'Libertinus_Keyboard';
  src: url('../../../public/fonts/LibertinusKeyboard-Regular.woff2') format('woff2'),
       url('../../../public/fonts/LibertinusKeyboard-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

//Lobster

@font-face {
  font-family: 'Lobster';
  src: url('../../../public/fonts/Lobster-Regular.woff2') format('woff2'),
       url('../../../public/fonts/Lobster-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

//Roboto

@font-face {
  font-family: 'Roboto';
  src: url('../../../public/fonts/Roboto-Regular.woff2') format('woff2'),
       url('../../../public/fonts/Roboto-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}

//Roboto-Bold

@font-face {
  font-family: 'Roboto-Bold';
  src: url('../../../public/fonts/Roboto-Bold.woff2') format('woff2'),
       url('../../../public/fonts/Roboto-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
}
